body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  background-image: url("logo.eddae0ff.png");
  width: 40px;
  height: 40px;
  margin: 10px 50px;
  display: block;
}

.header__nav {
  justify-content: center;
  align-items: center;
  gap: 20px;
  height: 60px;
  margin-right: 50px;
  display: flex;
}

.header__link {
  text-transform: uppercase;
  color: #060b35;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
  position: relative;
}

.header__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  align-items: center;
  height: 100%;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.star {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.star__wrapper {
  display: flex;
}

.star__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.star__reviews {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
}

.star--1 .star__star:nth-child(-n+1), .star--2 .star__star:nth-child(-n+2), .star--3 .star__star:nth-child(-n+3), .star--4 .star__star:nth-child(-n+4), .star--5 .star__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 198px;
  height: 406px;
}

.card:hover {
  transition-duration: .3s;
  transform: scale(1.2);
}

.card__wrapper {
  flex-direction: column;
  align-items: center;
  margin-top: 32px;
  display: flex;
}

.card__product {
  background: url("imac.202c52ff.jpeg") 0 0 / 160px 134px no-repeat;
  width: 160px;
  height: 134px;
  display: block;
}

.card .content {
  margin-left: 16px;
  margin-right: 16px;
}

.card .content__wrapper {
  margin-top: 40px;
}

.card .content__title {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card .content__product-code {
  color: #616070;
  align-items: start;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card .content__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card .content__price-label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card .content__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  margin-left: 16px;
  margin-right: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  width: 164px;
  height: 38px;
}

.card:hover .content__title {
  color: #34568b;
}

.container {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 300px) {
  .container {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}
